/******************/
/* Cho trình duyệt WebKit (Chrome, Safari, Edge mới) */
::-webkit-scrollbar {
    display: none;
}

/* Cho Firefox */
html {
    scrollbar-width: none;
}

/* Cho Internet Explorer & Edge cũ */
body {
    -ms-overflow-style: none;
}

/******************/

.nav {
    height: 40px;
    width: 100%;
    background-color: #0c70cf;
    font-size: 20px;
    font-weight: bolder;
    color: white;
    line-height: 40px;
}

.content {
    display: flex;
}

.input-container {
    display: flex;
    justify-content: center;
    flex-direction: row;
    margin-top: 20px;
    & > div {
        margin: 0 10px;
    }
}

.input-file,
.input-btn {
    height: 40px;
    background-color: #0c70cf;
}

label {
    color: #ffffff;
    font-weight: bolder;
    cursor: pointer;
    height: 40px;
    display: block;
    line-height: 40px;
    padding: 0 10px;
    border-radius: 5px;

    &:hover {
        background-color: #014788;
    }
}

.table {
    width: 100%;
    margin-bottom: 20px;
    background-color: beige;
    margin-left: auto;
    margin-right: auto;
}

.read-table-container {
    width: 50vw;
}

.input-table-container {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    margin-top: 30px;
}

.output-table-container {
    width: 50vw;
}
.thead {
    background-color: #0c70cf;
    color: white;
    font-weight: bolder;
}

/*************output-table***************/

.output-table {
    width: 80% !important;
    margin-bottom: 20px;
    background-color: beige;
    margin-left: auto;
    margin-right: auto;
    border-collapse: collapse; /* Sửa lỗi từ "table-collapse" thành "border-collapse" */
}

.table th,
.table td {
    border: 1px solid gray; /* Viền của các ô trong bảng */
    padding: 8px; /* Tạo khoảng cách giữa nội dung và viền */
    text-align: center; /* Căn giữa nội dung trong ô */
}

.output-table-container {
    width: 50vw;
}

